import React, { Component } from 'react';
import { Row, Col, Form } from 'antd';
import { BtnSearch, BtnClear } from 'components/common/button';
import { SelectAjax } from 'components/common';
import { getDormitorys, getDormitoryFloors } from 'services/dormitory';
import { withRouter } from 'react-router';
import { getQueryParams, updateQueryParams } from 'utils/urlUtil';
import { requiredRule } from 'utils/validateRules';

@Form.create({ name: 'FormSearchDormitoryRoom' })
class FormSearchDormitoryRoom extends Component {
  state = {};

  componentDidMount = () => {
    const { form } = this.props;
    form.setFieldsValue(getQueryParams());
  };

  formItemLayout = 6;

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      updateQueryParams(values);
    });
  };

  handleReset = () => {
    this.props.form.resetFields();
    updateQueryParams();
  };

  render() {
    const { getFieldDecorator, getFieldValue, setFieldsValue } = this.props.form;
    const dormId = getFieldValue('dormId');
    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <Col span={this.formItemLayout}>
            <Form.Item label="Tòa KTX">
              {getFieldDecorator('dormId', {
                rules: [requiredRule],
              })(<SelectAjax service={getDormitorys} />)}
            </Form.Item>
          </Col>
          <Col span={this.formItemLayout}>
            <Form.Item label="Tầng">
              {getFieldDecorator('floorId', {
                rules: [requiredRule],
              })(
                <SelectAjax
                  setFieldsValue={setFieldsValue}
                  params={{ dormId: getFieldValue('dormId') }}
                  service={getDormitoryFloors}
                  allowGetData={!!dormId}
                />
              )}
            </Form.Item>
          </Col>

          <Col span={this.formItemLayout}>
            <div className="btn-group">
              <BtnSearch htmlType="submit" />
              <BtnClear onClick={this.handleReset} />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default withRouter(FormSearchDormitoryRoom);



