import React, { Component } from 'react';
import { withRouter, Link } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import { BtnAddNew } from 'components/common/button';
import routeConstant from 'routes/route/routeConstant';

import { CartHeadAction, TableData, ColEditDelete } from 'components/common';
import { Card, Row, Col, message } from 'antd';
import { getDormitoryRooms, deleteDormitoryRoom } from 'services/dormitoryRoom';
import { moneyToString } from 'utils/formatUtil';
import { createQueryParams, getQueryParams } from 'utils/urlUtil';
import FormSearchDormitoryRoom from './FormSearchDormitoryRoom';

class ListDormitoryRoomPage extends Component {
  state = {
    reload: 0,
    paramSearch: {},
  };

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  handleChangeQueryParams = () => {
    this.setState({ paramSearch: getQueryParams() });
  };

  handleDelete = async ({ dormId, floorId, roomId }) => {
    await deleteDormitoryRoom({ dormId, floorId, roomId });
    message.success('Xóa thành công');
    this.setState({ reload: this.state.reload + 1 });
  };

  render() {
    const columns = [
      {
        title: 'Số phòng',
        dataIndex: 'roomNo',
        key: 'roomNo',
      },
      {
        title: 'Sức chứa',
        dataIndex: 'size',
        key: 'size',
      },
      {
        title: 'Thiết bị',
        dataIndex: 'device',
        key: 'device',
      },
      {
        title: 'Số tiền/người',
        dataIndex: 'feePerPerson',
        key: 'feePerPerson',
        render: text => moneyToString(text),
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: (text, record) => (
          <ColEditDelete
            linkEdit={{
              to: {
                pathname: routeConstant.DANH_MUC__KTX__PHONG__NHAP_DU_LIEU,
                search: createQueryParams({
                  id: record.id,
                  dormitoryId: record.dormitoryId,
                  floorId: record.floorId,
                }),
              },
            }}
            onConfirmDelete={() =>
              this.handleDelete({
                dormId: record.dormitoryId,
                floorId: record.floorId,
                roomId: record.id,
              })
            }
          />
        ),
      },
    ];

    const { paramSearch = {} } = this.state;

    return (
      <Card title="Phòng KTX">
        <CartHeadAction>
          <Link to={routeConstant.DANH_MUC__KTX__PHONG__NHAP_DU_LIEU}>
            <BtnAddNew />
          </Link>
        </CartHeadAction>
        <Row>
          <Col span={24}>
            <FormSearchDormitoryRoom />
          </Col>
          <Col span={24}>
            <TableData
              reload={this.state.reload}
              service={getDormitoryRooms}
              paramSearch={this.state.paramSearch}
              allowGetData={!!paramSearch.floorId}
              columns={columns}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <ListDormitoryRoomPage {...props} />
  </Page>
));



